export default {
	state: {
		isLogin: false,
		privilegeList:[],			//所有权限列表
		asideMenuList:[],			//菜单列表
		codeList:[],					//权限列表
		userInfo:{},					//用户信息
		roleInfo:{}						//角色信息

	},
	mutations: {
		getUserInfoSuccess (state, data) {
			let asideMenuList=[];
			let codeList=[];
			data.privilegeSet.forEach(item => {
				if(item.pri_type===1){
					asideMenuList.push(item);
			 }else if(item.pri_type===2){
				 codeList.push(item);
			 }else{
				 return ;
			 }
			});
			state.privilegeList = data.privilegeSet;
			state.asideMenuList = asideMenuList;
			state.codeList = codeList;
			state.userInfo =data.userInfo;
			state.roleInfo=data.roleInfo;
    },
    getUserInfoFailed (state) {
      state.isLogin = false;
    },
	},
	actions: {
		// 获取用户信息来判断有没有登录
    getUserInfo({commit}, params){
    	return new Promise((resolve, reject) => {
    		this._vm.ajax({
				  method: 'post',
				  url: httpConfig+'getuserinfo',
				  data: {
				  }
				}).then(function (response) {
					let data=response.data.data;
					if(data){
						commit('getUserInfoSuccess', data);
						resolve(response);
					}else{
						resolve(response);
					}
					// let isLogin = response.data.data.isLogin;
			   
			  })
			  .catch(function (error) {
			    commit('getUserInfoFailed', params);
			  });
      })
    	
    },
	},
	getters: {
		isLogin: (state) => state.isLogin,
		privilegeList: (state) => state.privilegeList,
		asideMenuList: (state) => state.asideMenuList,
		codeList: (state) => state.codeList,
		userInfo: (state) => state.userInfo,
		roleInfo: (state) => state.roleInfo,
	}
};


