import Vue from 'vue';
import Router from 'vue-router';

const Login = () => import('@/components/login');
const Admin = () => import('@/components/admin');
const Index = () => import('@/components/module/index');
const  Manage= () => import('@/components/business/manage');
const  Rate= () => import('@/components/business/rate');
const  Account= () => import('@/components/permission/account');
const  Role= () => import('@/components/permission/role');

const Pay = () => import('@/components/pay');


Vue.use(Router);

export default new Router({
  routes: [
  	{
  		path: '/login',
  		name: 'login',
  		component: Login
		},
		{
  		path: '/pay',
  		name: 'pay',
  		component: Pay
  	},
    {
    	path: '/',
    	component: Admin,
    	children: [
    		{
    			path: '',
    			name: 'index',
					component: Index,
					meta:{
						remark:[]
					}
				},
				{
					path: '/manage',
					name: 'Manage',
					component: Manage,
					meta:{
						remark:["业务查询","业务办理"]
					}
				},
				{
					path: '/rate',
					name: 'Rate',
					component: Rate,
					meta:{
						remark:["业务查询","费率设置"]
					}
				},
				{
					path: '/role',
					name: 'Role',
					component: Role,
					meta:{
						remark:["权限管理","角色管理"]
					}
				},
				{
					path: '/account',
					name: 'Account',
					component: Account,
					meta:{
						remark:["权限管理","账户管理"]
					}
				},
    	]
		},
	
  ]
});



